 .btn {
  display: inline-block;
  cursor: pointer;
  box-sizing: border-box;
  margin-left: 0;
  // padding: 0.6rem 0.8rem 0.6rem 1rem; // totally unsure of why that right padding and font-metrics are bonked
  border-radius: 1px;
  text-align: center;
  background-color: transparent;
  border-radius: 0 0 0 1.5rem;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  position: relative;
  border: 0;

  &::before {
    content: "";
    height: 100%;
    width: 100%;
    background-color: $color-kennedy;
    border: 0.1rem solid $color-kennedy;
    border-radius: 0 0 0 1.1rem;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    transform:translate(0.25rem, 0.25rem);
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease;
  }

  &::after {
    content: "";
    background-color: $color-sunshine;
    border: 0.1rem solid $color-sunshine;
    border-radius: 0 0 0 1.1rem;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease;
  }

  .button-content, .icon-button__content, .button__content, .btn__content,
  h3 {
    padding: 0.6rem 0.7rem 0.55rem;
    color: $color-charcoal!important;
    position: relative;
    z-index: 2;
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease;
    font-size: 1rem;

  }

  &:hover {
    &::after {
      transform: translate3d(-5px, -5px, 0px);
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease;
    }

    &::before {
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease;
    }

    .button-content, .icon-button__content, .button__content, .btn__content, h3 
     {
      transform: translate3d(-5px, -5px, 0px);
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease;
    }

  }

  &.secondary {
    background-color: transparent;

    &::before {
      background-color: $color-white;
      border: 0.15rem solid $color-kennedy;
    }

    &::after {
      background-color: $color-white;
      border: 0.15rem solid $color-kennedy;
    }

    &:hover {
      &::after {}

      &::before {
        background-color: $color-kennedy-trans;
      }

      h6 {}

    }
  }

}


